import React, {useState} from 'react'
import {CopyToClipboard} from 'react-copy-to-clipboard';

export const Url = (props) => {
    const [copied, setCopied] = useState(false)
    const[text, setText] = useState('')

    const handleCopy =() => {
        setText(props.url.shorten)
        setCopied(true)
    }
    let btn_class = copied ? "dark" : ""
    return (
        <>
            <li>
                <a>{props.url.url}</a><a>{props.url.shorten}</a>
                <CopyToClipboard 
                text={text}
                onCopy={handleCopy}>
                    <button className={btn_class}>{copied ? 'Copied!' : 'copy'}</button>
                </CopyToClipboard>
            </li>
            {/*  */}
        </>
    )
}
