import React, {useState, useContext} from 'react'
import axios from 'axios';
import { Context } from '../GlobalContext/Context'

export const Shortnening = () => {
    const [url, seturl] = useState("")
    const {addLink} = useContext(Context)
    
    const handleSubmit = (event) => {
        event.preventDefault();
        
        axios.post('https://rel.ink/api/links/', {url: url})
        .then(res => {//console.log(res.data)
            let shorten = `https://rel.ink/${res.data.hashid}`
            const newUrl = ({
                id: Math.floor(Math.random() * 10000),
                url,
                shorten
            });
    
            addLink(newUrl);
        })
        .catch(e=>console.log(e));
        //console.log(shorten)
        
        // const newUrl = ({
        //     id: Math.floor(Math.random() * 10000),
        //     url,
        //     shorten: shorten
        // });

        // addLink(newUrl);
        seturl('')
    }
    return (
        <div className="shorten">
            <div className="shorten-form">
                <form onSubmit={handleSubmit}>
                    <input type="text" value={url} name="" onChange={(event)=>seturl(event.target.value)}/>
                    <button>Shorten It!</button>
                </form>
            </div>
        </div>
    )
}
