import React from 'react'
import rec from "../images/icon-brand-recognition.svg"
import de from "../images/icon-detailed-records.svg"
import com from "../images/icon-fully-customizable.svg"
import { UrlList } from './UrlList'

export const Stats = () => {
    return (
        <div className="stat">
            <UrlList />
            <div className="stat-content">
                <h1><strong>Advanced Statistics</strong></h1>
                <p>Track how your links are performing across the web<br/>with out advanced statistics dashboard</p>
                <div className="cards">
                    <div className="card first">
                        <div className="tag"><img src={rec}/></div>
                        <h3>Brand Recognition</h3>
                        <p>Boost your brand recognition with each click. Generic links don't mean a thing. Branded links help insil confidence in your content.</p>
                    </div>
                    <div className="line"></div>
                    <div className="card second">
                        <div className="tag"><img src={de}/></div>
                        <h3>Detailed Records</h3>
                        <p>Gain insights into who is clicking your links. Knowing when and where people engage with your content helps inform better decisions</p>
                    </div>
                    <div className="card third">
                        <div className="tag"><img src={com}/></div>
                        <h3>Fully Customizable</h3>
                        <p>Improve brand awareness and content discoverability through customizable links, supercharging audience engagement</p>
                    </div>
                </div>
            </div>      
                 
        </div>
    )
}
