import React, {useContext} from 'react'
import { Context } from '../GlobalContext/Context'
import { Url } from './Url'

export const UrlList = () => {
    const {urls} = useContext(Context)
    if(urls === undefined) {
        return (<h1>Hello</h1>)
    }
    return (
        <ul className="list">
            {urls.map(item => <Url 
                key={item.id}
                url={item}
                />
            )}
        </ul>
    )
}
